<template>
  <div class="pos">
    <el-row>
      <el-col :span="7" class="posOrder">
        <el-tabs>
          <el-tab-pane label="点餐">
            <el-table :data="tableData" border>
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="count" width="60px" label="数量"></el-table-column>
              <el-table-column prop="price" width="70px" label="金额"></el-table-column>
              <el-table-column label="操作" width="120px" fixed="right">
                <template scope="scope">
                  <el-button type="text" size="small" @click="del(scope.row)">删除</el-button>
                  <el-button type="text" size="small" @click="add(scope.row)">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="xxx">
              数量:{{totalCount}} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;金额:{{totalMoney}}
            </div>
            <div style="margin-top: 20px">
              <el-button type="primary">挂单</el-button>
              <el-button type="danger" @click="delAll">删除</el-button>
              <el-button type="success" @click="jiezhang">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">

          </el-tab-pane>
          <el-tab-pane label="外卖">

          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span="17" class="posRight">
        <div class="posRightContent">
          <div class="title">常用商品</div>
          <div class="goodsList">
            <ul>
              <li v-for="goods in goods" @click="add(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="goodsPrice">¥{{goods.price}}元</span>
              </li>
            </ul>
          </div>
        </div>

        <div class="goodsType">
          <el-tabs>
            <el-tab-pane label="汉堡">
              <div class="hanbao">
                <ul>
                  <li v-for="goods in hanbao" @click="add(goods)">
                    <span class="imgstyle"><img src="@/assets/img/香辣鸡腿堡.jpg" alt="wdnmd"/> </span>
                    <span class="titlestyle">{{goods.goodsName}}</span>
                    <span class="pricestyle">¥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="小吃">
              <div class="hanbao">
                <ul>
                  <li v-for="goods in xiaochi" @click="add(goods)">
                    <span class="imgstyle"><img src="@/assets/img/魔法鸡块.jpg" alt="wdnmd"/> </span>
                    <span class="titlestyle">{{goods.goodsName}}</span>
                    <span class="pricestyle">¥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <div class="hanbao">
                <ul>
                  <li v-for="goods in yinliao" @click="add(goods)">
                    <span class="imgstyle"><img src="@/assets/img/可乐大杯.jpg" alt="wdnmd"/> </span>
                    <span class="titlestyle">{{goods.goodsName}}</span>
                    <span class="pricestyle">¥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <div class="hanbao">
                <ul>
                  <li v-for="goods in taocan" @click="add(goods)">
                    <span class="imgstyle"><img src="@/assets/img/快乐全家桶.jpg" alt="wdnmd"/> </span>
                    <span class="titlestyle">{{goods.goodsName}}</span>
                    <span class="pricestyle">¥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
    export default {
        name: 'pos',
        data() {
            return {
                tableData: [],
                goods: [
                    {
                        goodsId: 1,
                        goodsName: '香辣鸡腿堡',
                        price: 18
                    }, {
                        goodsId: 2,
                        goodsName: '田园鸡腿堡',
                        price: 15
                    }, {
                        goodsId: 3,
                        goodsName: '和风汉堡',
                        price: 15
                    }, {
                        goodsId: 4,
                        goodsName: '快乐全家桶',
                        price: 80
                    }, {
                        goodsId: 5,
                        goodsName: '脆皮炸鸡腿',
                        price: 10
                    }, {
                        goodsId: 6,
                        goodsName: '魔法鸡块',
                        price: 20
                    }, {
                        goodsId: 7,
                        goodsName: '可乐大杯',
                        price: 10
                    }, {
                        goodsId: 8,
                        goodsName: '雪顶咖啡',
                        price: 18
                    }, {
                        goodsId: 9,
                        goodsName: '大块鸡米花',
                        price: 15
                    }, {
                        goodsId: 10,
                        goodsName: '香脆鸡柳',
                        price: 17
                    }, {
                        goodsId: 11,
                        goodsName: '醇香土豆泥',
                        price: 10
                    }, {
                        goodsId: 12,
                        goodsName: '劲爆鸡米花',
                        price: 10
                    }, {
                        goodsId: 13,
                        goodsName: '鸡柳饭',
                        price: 20
                    }, {
                        goodsId: 14,
                        goodsName: '安心油条',
                        price: 6
                    }, {
                        goodsId: 15,
                        goodsName: '皮蛋瘦肉粥',
                        price: 8
                    }, {
                        goodsId: 16,
                        goodsName: '半桔半柠鲜果茶',
                        price: 12
                    }, {
                        goodsId: 17,
                        goodsName: '嫩牛五方',
                        price: 15
                    }, {
                        goodsId: 18,
                        goodsName: '可乐',
                        price: 6
                    }, {
                        goodsId: 19,
                        goodsName: '藤椒脆皮鸡',
                        price: 20
                    }, {
                        goodsId: 20,
                        goodsName: '熏鸡法风烧饼',
                        price: 12
                    }
                ],
                hanbao: [
                    {
                        goodsId: 1,
                        goodsName: '香辣鸡腿堡',
                        price: 16
                    }, {
                        goodsId: 2,
                        goodsName: '田园鸡腿堡',
                        price: 15
                    }, {
                        goodsId: 3,
                        goodsName: '和风汉堡',
                        price: 15
                    }
                ],
                xiaochi: [
                    {
                        goodsId: 5,
                        goodsName: '脆皮炸鸡腿',
                        price: 10
                    }, {
                        goodsId: 6,
                        goodsName: '魔法鸡块',
                        price: 20
                    },
                ],
                yinliao: [
                    {
                        goodsId: 7,
                        goodsName: '可乐大杯',
                        price: 10
                    }, {
                        goodsId: 8,
                        goodsName: '雪顶咖啡',
                        price: 18
                    }
                ],
                taocan: [
                    {
                        goodsId: 4,
                        goodsImg: "",
                        goodsName: '快乐全家桶',
                        price: 80
                    }
                ],
                totalCount: 0,
                totalMoney: 0
            }
        },
        methods: {
            add(goods) {
                this.totalCount = 0;
                this.totalMoney = 0;
                let isHave = false;
                for (let i = 0; i < this.tableData.length; i++) {
                    if (this.tableData[i].goodsId == goods.goodsId) {
                        isHave = true;
                    }
                }
                if (isHave) {

                    let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
                    arr[0].count++;

                } else {

                    let newGoods = {goodsId: goods.goodsId, goodsName: goods.goodsName, price: goods.price, count: 1};
                    this.tableData.push(newGoods);

                }
                this.allmoney();

            },
            del(goods) {
                this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
                this.allmoney();
            },
            delAll() {
                this.tableData = [];
                this.totalMoney = 0;
                this.totalCount = 0;
            },
            jiezhang() {
                if (this.tableData != 0) {
                    this.tableData = [];
                    this.totalCount = 0;
                    this.totalMoney = 0;
                    this.$confirm('亲，是否下单？', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'info',
                        center: true
                    }).then(() => {
                        this.$message({
                            type: 'success',
                            message: '谢谢老板，老板大气！'
                        });
                    }).catch(() => {
                        this.$message({
                            type: 'error',
                            message: '下次再来嗷！'
                        });
                    });
                }
                else{
                    this.$message.error('不要再点了！')
                }
            },
            allmoney() {
                this.totalCount = 0;
                this.totalMoney = 0;
                if (this.tableData) {
                    this.tableData.forEach((element) => {
                        this.totalCount += element.count;
                        this.totalMoney = this.totalMoney + (element.price * element.count);
                    });
                }
            }
        }
    }
</script>

<style>
  .posOrder {
    background-color: #ffffff;
    height: 700px;
    border-right: 1.2px solid lightgrey;
  }

  .el-tabs__nav {
    margin-left: 15px;
  }

  .posRight {
    background-color: ghostwhite;
    height: 700px;
  }

  .posRightContent .title {
    height: 39px;
    text-align: left;
    padding-left: 20px;
    line-height: 40px;
    border-bottom: 1.2px solid #D3dce6;
  }

  .goodsList ul li {
    list-style: none;
    border: 1px solid lightgrey;
    padding: 10px;
    background-color: white;
    float: left;
    margin: 15px;
  }

  .goodsPrice {
    color: dodgerblue;
  }

  .goodsType {
    clear: both;
  }

  .hanbao ul li {
    list-style: none;
    border: 1px solid #E5E9F2;
    background-color: #fff;
    float: left;
    margin: 10px;
    padding: 2px;
  }

  .hanbao ul li span {
    float: left;
    display: block;
  }

  .imgstyle img {
    width: 80px;
    height: 80px;
  }

  .titlestyle {
    color: red;
    margin-left: 10px;
    margin-top: 10px;
  }

  .pricestyle {
    margin-top: 45px;
    margin-left: -60px;
  }

  .xxx {
    width: 100%;
    height: 40px;
    background-color: white;
    border-bottom: 1.2px solid lightgrey;
    line-height: 40px;
  }
</style>




