<template>
  <div id="app">
    <LeftNav></LeftNav>
    <div class="main">
      <pos></pos>
    </div>
  </div>
</template>

<script>
    import LeftNav from './components/LeftNav';
    import pos from './components/POS';
    export default {
        name: 'App',
        components: {
            LeftNav,
            pos
        }
    }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  .main{
    float: left;
    width: 85%;
    height: 700px;
  }
</style>



